export function defineDay(day) {
    const dayInLowerCase = day.toLowerCase();
    
    switch (dayInLowerCase) {
        case 'sunday':
            return '';
        case 'monday':
            return '';
        case 'tuesday':
            return '';
        case 'wednesday':
            return '';
        case 'thursday':
            return '';
        case 'friday':
            return '';
        case 'saturday':
            return '';
        default:
            return day;
    }
}